.footer {
  @apply flex flex-wrap mt-10;

  &__column {
    @apply w-full;

    &--left {
      @screen xl {
        @apply w-2/5;
      }
    }
    &--right {
      @screen xl {
        @apply w-3/5;
      }
    }
  }

  &__heading {
    @apply mb-2 uppercase text-color--mysterious;
  }

  &__list {
    @apply list-reset hidden;
    @screen sm {
      @apply block;
    }
  }

  &__list-item {
    @apply text-size--small text-grey-dark;
  }

  &__link {
    @apply no-underline text-grey-dark;
  }

  &__social-media-wrap {
    @apply flex justify-between;
  }

  &__content {
    @apply text-right;
  }

  &__back-to-top {
    @apply bg-white inline-block shadow-md text-size--large p-2 mb-4 rounded;
  }

}
