/**
 * Document Base Styles
 *
 * This contains the base unit measurements for type and line-heights
 * Giving the document height 100%
 * Setting the base font family for the whole project
 *
 * ========================================================================= */
html, body {
  height: 100%;
  position: relative; }

body {
  width: 100%; }

/**
 * Set Base Font family and size
 */
html {
  font-size: 16px;
  font-size: 100%;
  font-family: Montserrat--regular, helvetica neue;
  color: #515E63; }

/**
 * Set Base Line Height
 */
*, button, input, optgroup, select, textarea {
  line-height: 16px;
  line-height: 1rem; }

/* Helpers */
.layout-block {
  background-color: rgba(164, 164, 164, 0.38);
  padding: 0.5rem;
  border: 1px dashed #969696;
  position: relative; }
  .layout-block--nopadding {
    padding: 0; }
  .layout-block--margin-top {
    margin-top: 10px; }
  .layout-block--1x1 {
    flex: 1 0 auto; }
  .layout-block--1x1:after {
    content: "";
    display: block;
    padding-bottom: 100%; }
  .layout-block--4x3 {
    flex: 1 1 auto; }
  .layout-block--4x3:after {
    content: "";
    display: block;
    padding-bottom: 75%; }
  .layout-block--16x9 {
    flex: 1 1 auto; }
  .layout-block--16x9:after {
    content: "";
    display: block;
    padding-bottom: 56.25%; }
  .layout-block__content {
    padding: 0.5rem;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }

/**
 * Line Height Grid
 *
 * This will print a line-height grid on the page,
 * based on the base document line height.
 *
 * This is used to ensure that all your components are harmoniously aligned
 *
 * .line-height-grid--absolute - Allows you to overlay the grid.
 *                               Ensure parent is relative
 *
 * ========================================================================= */
/**
 * Line-height grid
 */
.line-height-grid, .line-height-grid--double {
  position: absolute;
  background-image: linear-gradient(to bottom, rgba(0, 170, 255, 0.3) 1px, transparent 1px);
  background-position: left top;
  background-repeat: repeat;
  background-size: 100% 16px;
  background-size: 100% 1rem; }
  .line-height-grid--absolute {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%; }

/**
* Double the amount of lines
* Half the base spacing ie 16 => 8
*/
.line-height-grid--double {
  background-image: linear-gradient(to bottom, rgba(0, 170, 255, 0.3) 1px, transparent 1px, transparent 8px, rgba(0, 170, 255, 0.3) 8px, transparent 9px, transparent 16px); }

